.grade {
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 5rem;
  width: 80%;
  padding: 0 10% 0 10%;
  margin: 1rem;
  background-color: #fcfcfc;
  position: relative;
  transition: .2s;
}

.grade::before {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translateX(-50%);
  height: .3rem;
  width: 100%;
  border-radius: .1rem;
  background-color: #80BFB4;
  opacity: .2;
  transition: .2s;
}

.gradeNum_wrap {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  justify-content: space-around;
  gap: .2rem;
}

.classNum_wrap {
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  gap: 0;
}

.grade h2 {
  font-size: 2.5rem;
  transition: .2s;
}

.grade select {
  cursor: pointer;
  color: #80BFB4;
  outline: none;
  font-size: 3.5rem;
  transform: translate(20%, 8%);
  transition: .2s;
}

.grade select:hover {
  color: #D9A3D0;
}

.grade:has(select:hover) {
  background-color: #ececec;
}

.grade:has(select:hover)::before {
  opacity: 0;
}

.grade .deleteBtn {
  display: none;
  position: absolute;
  right: 0;
  bottom: 10%;
  height: 60%;
  width: 4em;
  opacity: 0.9;
  cursor: pointer;
}

.grade .deleteBtn.enabled {
  display: block;
  background-image: url('.\/dustbox1.svg');
  background-size: contain;
  background-repeat: no-repeat;
  cursor: pointer;
}

.grade .deleteBtn.enabled:hover {
  background-image: url('.\/dustbox2.svg')
}

.grade:has(.deleteBtn.enabled:hover) {
  background-color: #ececec;
}

.grade_wrap .addBtn {
  display: none;
  font-size: 6rem;
  transform: translateY(-2rem);
  cursor: pointer;
}

.grade_wrap .addBtn.enabled {
  display: block;
}

@media (max-width: 767px) {
  .grade {
    height: 4rem;
  }
  .grade h2 {
    font-size: 1.5rem;
  }
  .grade select {
    font-size: 2rem;
  }
  .grade_wrap .addBtn {
    font-size: 4rem;
  }
}