.curtain {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  background: black;
  opacity: .8;
}

.curtain p {
  color: #fff;
  font-size: 2rem;
}

.curtain.hide {
  display: none;
}

@media (max-width: 767px) {
  .curtain {
    display: block;
  }
}