.calendar {
  width: 80%;
  text-align: center;
}

.calendar .header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-bottom: 1.5rem;
}

.calendar .header span {
  font-size: 5rem;
  cursor: pointer;
  color: #80BFB4;
  transition: .2s;
}

.calendar .header span:hover {
  color: #D9A3D0;
  font-weight: bold;
}

.calendar .header h2 {
  font-size: 3.5rem;
}

.calendar-grid {
  display: grid;
  grid-template-columns: repeat(7, 1fr); /* 1週間を7列に設定 */
  gap: .5rem;
}

.calendar-cell {
  width: 100%;
  height: 4rem;
  font-size: 1.7rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  cursor: pointer;
  background-color: #fcfcfc;
  transition: .2s;
}

.calendar-cell:hover {
  background-color: #ececec;
}

.calendar-cell.selected {
  background-color: #F2B366; /* 選択時の背景色 */
  color: #F2F1F0;
}

.calendar-cell.selected:hover {
  background-color: #E2A356;
  color: #F2F1F0;
}

.calendar-cell.disabled {
  background-color: #dddddd;
  cursor: default;
}

@media (max-width: 767px) {
  .calendar .header span {
    font-size: 2rem;
  }

  .calendar .header h2 {
    font-size: 1.5rem;
  }

  .calendar-cell {
    height: 2rem;
    font-size: 1rem;
  }
}
