@font-face {
  font-family: 'Ronde';
  src: url('../assets/fonts/Ronde-B_square.otf');
}
* {
  font-family: 'Ronde';
  color: #260124;
}

::-webkit-scrollbar {
width: 1rem;
height: 1rem;
}
::-webkit-scrollbar-track {
background-color: #a0dfd4;
border-radius: 3rem;
}
::-webkit-scrollbar-thumb {
background: #80BFB4;
border-radius: 3rem;
width: 1rem;
}

html {
  font-size: 62.5%; /* 1rem = 10px */
  user-select: none;
  background: #F2F1F0
}