.container {
  display: flex;
  flex-direction: column;
  height: 100vh;
  width: 100vw;
}

main {
  position: fixed;
  top: 0;
  left: 0;
  padding-top: 6rem; 
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-columns: 3fr 7fr;
}

.editor {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  overflow-y: auto;
}

.calendar_wrap {
  height: auto;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 5rem;
}

.grade_wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: start;
  margin-top: 5rem;
  margin-bottom: 5rem;
  width: 100%;
}

.PDFview {
  height: 100%;
  width: 100%;
}

.curtain {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
}

@media (max-width: 767px) {
  main {
    grid-template-columns: 1fr 1fr;
  }
}